.container {
  align-items: center;
  justify-content: center;
  text-align: center;
}

.content {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 400px;
  padding: 30px 0;
  width: 100%;
}

.section {
  display: flex;
  flex-direction: column;
  text-align: left;
  width: 100%;
}